.footer-top {
  color: #333333;
  padding: 3.125rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 3.75rem;
  }
  &__right {
    flex-shrink: 0;
    display: flex;
    gap: 3.75rem;
  }
  &__left {
    display: flex;
    align-items: center;
    gap: 1.125rem;
  }
  &__logo {
    width: 4.375rem;
    flex-shrink: 0;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 14.375rem;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.5rem;
    p {
      font-size: 1rem;
      line-height: 130%;
      margin: 0;
    }
    p:nth-child(2) {
      display: none;
    }
  }
  &__text2 {
    display: flex;
    gap: 3.75rem;
    p {
      text-align: right;
      font-size: 1rem;
      line-height: 130%;
      margin: 0;
    }
    span {
      display: block;
      color: #BFBFBF;
      margin-bottom: 0.3125rem;
    }
  }
  &__wrap {
    display: flex;
    gap: 3.75rem;
  }
  &__socials {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    &-text {
      text-align: right;
      font-size: 1rem;
      line-height: 130%;
      color: #BFBFBF;
      margin-bottom: 0.3125rem;
    }
    &-wrap {
      display: flex;
      justify-content: flex-end;
      gap: 0.625rem;
    }
  }
  &__social {
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.125rem solid #E0D2C1;
    border-radius: 0.625rem;
    &:hover {
      background: #E0D2C1;
    }
  }
  &__group {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__phone {
    span {
      display: block;
      text-align: right;
      font-size: 1rem;
      line-height: 130%;
      color: #BFBFBF;
      margin-bottom: 0.3125rem;
    }
    a {
      font-size: 1.25rem;
      line-height: 130%;
      font-weight: 500;
    }
    a:hover {
      color: #CFAA96;
    }
  }
  &__btn {
    background: transparent;
    margin-top: 0.3125rem;
    span {
      min-width: unset;
      border-bottom: 0.0625rem solid #CFAA96;
      padding: 0;
      font-size: 1rem;
      line-height: 130%;
      font-weight: 600;
      color: #CFAA96;
      transition: all 0.3s;
    }
    &:hover {
      span {
       opacity: 0.7;
      }
    }
  }
}
.footer-bottom {
  background: #F1F1F1;
  padding: 0.75rem 0;
  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
  }
  .b2b {
    width: 13.875rem;
    display: block;
    img {
      width: 100%;
    }
  }
  &__text {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.3125rem;
    p {
      font-size: 0.75rem;
      line-height: 130%;
      color: #000;
      margin: 0;
    }
    a {
      color: #CFAA96;
      border-bottom: 0.0625rem solid #CFAA96;
    }
    a:hover {
      color: #A5755B;
      border-color: #A5755B;
    }
  }
}


@media screen and (max-width: 1600px) {
  .footer-top {
    &__inner {
      gap: 2rem;
    }
    &__right {
      gap: 2rem;
    }
    &__text2 {
      gap: 2rem;
    }
    &__wrap {
      gap: 2rem;
    }
  }
}

@media screen and (max-width: 1399px) {
  .footer-top {
    padding: 2.5rem 0;
    &__text {
      max-width: 13rem;
      p {
        font-size: 0.875rem;
      }
    }
    &__text2 {
      p {
        font-size: 0.875rem;
      }
    }
    &__socials {
      &-text {
        font-size: 0.875rem;
      }
    }
    &__phone {
      span {
        font-size: 0.875rem;
      }
      a {
        font-size: 1.125rem;
      }
    }
  }
}

@media screen and (max-width: 1250px) {
  .footer-top {
    &__right {
      .footer-top__text2 {
        p:nth-child(1) {
          display: none;
        }
      }
    }
    &__text {
      p:nth-child(2) {
        display: block;
      }
    }
    &__group {
      align-items: start;
    }
  }
}

@media screen and (max-width: 1023px) {
  .footer-top {
    padding: 1.5rem 0;
    &__inner {
      gap: 1.5rem;
    }
    &__left {
      flex-shrink: 0;
    }
    &__right {
      flex-shrink: unset;
      gap: 1.5rem;
    }
    &__text {
      max-width: 10rem;
    }
    &__text2 {
      flex-wrap: wrap;
      gap: 1rem 2rem;
      p {
        text-align: start;
      }
      p:nth-child(1) {
        order: 1;
      }
    }
    &__wrap {
      flex-shrink: 0;
      gap: 1.5rem;
    }
    &__socials {
      align-items: flex-start;
      &-text {
        text-align: start;
      }
    }
    &__phone {
      span {
        text-align: start;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .footer-top {
    &__inner {
      flex-direction: column;
      align-items: start;
    }
    &__right {
      width: 100%;
      justify-content: space-between;
    }
    &__text {
      max-width: 13rem;
    }
  }
}

@media screen and (max-width: 576px) {
  .footer-top {
    &__right {
      flex-direction: column;
    }
    &__wrap {
      flex-direction: row;
      gap: 1rem;
    }
  }
}