h2 {
  font-size: 3rem;
  line-height: 110%;
  margin: 0;
}
.btn-beige {
  background: linear-gradient(0.00deg, rgb(207, 170, 150),rgb(165, 117, 91) 112.069%);
  border-radius: 5rem;
  &:hover {
    background: linear-gradient(180.00deg, rgb(207, 170, 150) 9.964%,rgb(165, 117, 91) 300.927%);
  }
  span {
    min-width: unset;
    padding: 1.125rem 2.5rem;
    font-size: 1.125rem;
    line-height: 130%;
    color: #fff;
  }
}
.swiper-button-prev,
.swiper-button-next {
  width: 4.9375rem;
  height: 4.9375rem;
  border: 0.0625rem solid rgb(51, 51, 51);
  border-radius: 50%;
  margin-top: 0;
  transition: all 0.3s;
  &:after {
    display: none;
  }
  &:hover {
    background: linear-gradient(0.00deg, rgb(207, 170, 150),rgb(165, 117, 91) 112.069%);
    border-color: rgb(165, 117, 91);
    svg path {
      stroke: #Fff;
    }
  }
  svg {
    max-width: 2.625rem;
    height: auto;
    transition: all 0.3s;
  }
}
.agreement-check {
  max-width: 21.875rem;
  input {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
  }
  input:checked ~ label {
    &:after {
      background-image: url("../img/check.svg");
    }
  }
  label {
    position: relative;
    font-size: 0.875rem;
    line-height: 130%;
    color: #ffffff;
    padding-left: 1.875rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 0.875rem;
      height: 0.875rem;
      background-repeat: no-repeat;
      background-position: center;
      background-size: auto;
      background-color: rgba(0, 0, 0, 0);
      border: 0.0625rem solid #fff;
      border-radius: 0.3125rem;
    }
  }
}
.popup {
  background: transparent !important;
  padding: 0 !important;
}
.navigation {
  padding: 3.125rem 0;
  ul {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.5rem;
    li a {
      font-size: 1.5rem;
      line-height: 130%;
      color: #333333;
      &:hover {
        color: #A5755B;
      }
    }
  }
}


@media screen and (max-width: 1399px) {
  h2 {
    font-size: 2.25rem;
  }
  .swiper-button-prev,
  .swiper-button-next {
    width: 3.125rem;
    height: 3.125rem;
    svg {
      max-width: 1.5rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  h2 {
    font-size: 2rem;
  }
  .btn-beige {
    span {
      font-size: 1rem;
    }
  }
}