#header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  color: #fff;
  border-bottom: 0.0625rem solid rgba(255, 255, 255, 0.5);
  padding: 0.75rem 0;
  .header__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 11.125rem;
  }
  .header__right {
    flex-grow: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 3rem;
  }
  .header__logo {
    width: 4.375rem;
    display: block;
    img {
      width: 100%;
    }
  }
  .header__text {
    display: flex;
    gap: 3rem;
    p {
      font-size: 1rem;
      line-height: 130%;
      margin: 0;
    }
  }
  .header__wrap {
    display: flex;
    gap: 3rem;
  }
  .header__socials {
    display: flex;
    gap: 0.875rem;
  }
  .header__social {
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.0625rem solid #fff;
    border-radius: 0.625rem;
    &:hover {
      background: #fff;
    }
  }
  .header__group {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .header__phone {
    font-size: 1.25rem;
    line-height: 130%;
    font-weight: 500;
    &:hover {
      color: #CFAA96;
    }
  }
  .header__btn {
    background: transparent;
    margin-top: 0.3125rem;
    span {
      min-width: unset;
      border-bottom: 0.0625rem solid #CFAA96;
      padding: 0;
      font-size: 1rem;
      line-height: 130%;
      font-weight: 600;
      color: #CFAA96;
      transition: all 0.3s;
    }
    &:hover {
      span {
        border-color: #fff;
        color: #fff;
      }
    }
  }
}

@media screen and (max-width: 1399px) {
  #header {
    .header__inner {
      gap: 6rem;
    }
    .header__right {
      gap: 1.5rem;
    }
    .header__text {
      gap: 1.5rem;
    }
    .header__wrap {
      gap: 1.5rem;
    }
    .header__phone {
      font-size: 1.125rem;
    }
  }
}

@media screen and (max-width: 1180px) {
  #header {
    .header__inner {
      gap: 4rem;
    }
  }
}

@media screen and (max-width: 1023px) {
  #header {
    .header__text {
      gap: 1.5rem;
      p {
        font-size: 0.75rem;
      }
    }
    .header__right {
      gap: 1.5rem;
    }
    .header__wrap {
      gap: 1.5rem;
    }
    .header__social {
      width: 2.25rem;
      height: 2.25rem;
      svg {
        width: auto;
        max-width: 1.125rem;
        height: auto;
      }
    }
    .header__phone {
      font-size: 1rem;
    }
    .header__btn {
      span {
        font-size: 0.875rem;
      }
    }
  }
}

@media screen and (max-width: 850px) {
  #header {
    .header__inner {
      gap: 1.5rem;
    }
  }
}

@media screen and (max-width: 768px) {
  #header {
    .header__right {
      flex-grow: unset;
    }
    .header__text {
      flex-wrap: wrap;
      gap: 0.625rem 1.5rem;
      p:nth-child(1) {
        display: none;
      }
    }
    .header__wrap {
      flex-shrink: 0;
    }
  }
}

@media screen and (max-width: 576px) {
  #header {
    .header__inner {
      gap: 1rem;
    }
    .header__wrap {
      gap: 1rem;
    }
    .header__socials {
      gap: 0.625rem;
    }
    .header__social {
      width: 1.875rem;
      height: 1.875rem;
      svg {
        max-width: 0.875rem;
      }
    }
    .header__phone {
      font-size: 0.875rem;
    }
  }
}

@media screen and (max-width: 450px) {
  #header {
    .header__text {
      display: none;
    }
  }
}