.main-section {
  background-image: url("../img/main-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #fff;
  padding: 16.875rem 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
  &__left {
    max-width: 40.875rem;
  }
  &__text {
    position: relative;
    max-width: 16.875rem;
    font-size: 1.25rem;
    line-height: 130%;
    margin-left: auto;
    i {
      position: absolute;
      top: -6.25rem;
      left: -3.75rem;
      width: 25rem;
      pointer-events: none;
      img {
        width: 100%;
      }
    }
  }
  h1 {
    font-size: 3rem;
    line-height: 110%;
    text-stroke: 0.125rem #FFFFFF;
    -webkit-text-stroke: 0.125rem #FFFFFF;
    margin: 2rem 0 0 auto;
  }
  &__subtitle {
    font-family: "Patriciana";
    font-size: 2rem;
    line-height: 130%;
    text-stroke: 0.0625rem #FFFFFF;
    -webkit-text-stroke: 0.0625rem #FFFFFF;
    margin-top: 1rem;
  }
  &__group {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    margin-top: 4rem;
  }
  &__btn {
    flex-shrink: 0;
  }
  &__info {
    font-size: 1.125rem;
    line-height: 130%;
  }
  &__group2 {
    position: relative;
    padding: 2.25rem 0 2.25rem 14.125rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 50rem;
      height: 100%;
      background: rgb(0 0 0 / 10%);
      backdrop-filter: blur(0.5rem);
      border-radius: 1.5rem;
      pointer-events: none;
    }
  }
  &__text2 {
    position: relative;
    z-index: 2;
    max-width: 19.375rem;
    text-align: right;
    margin-left: auto;
    p:nth-child(1) {
      font-size: 1.5rem;
      line-height: 130%;
      margin: 0;
    }
    p:nth-child(2) {
      font-size: 1.125rem;
      line-height: 130%;
      margin: 0.625rem 0 0 0;
    }
  }
  &__img {
    position: absolute;
    z-index: 1;
    top: -16.875rem;
    right: calc(100% - 28rem);
    max-width: 43.125rem;
    width: 36vw;
    pointer-events: none;
    img {
      width: 100%;
    }
  }
}
.info {
  padding: 0;
  &__items {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 2rem / 2));
    gap: 0 2rem;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    &:nth-child(1),
    &:nth-child(2) {
      .info__item-img {
        img {
          border-radius: 0 0 1.5rem 1.5rem;
        }
      }
    }
    &:nth-child(3),
    &:nth-child(4) {
      .info__item-img {
        order: -1;
        img {
          border-radius: 1.5rem 1.5rem 0 0;
        }
      }
    }
    &-img {
      max-width: 21.875rem;
      flex-shrink: 0;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &-text {
      flex-grow: 1;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1rem;
      p {
        max-width: 17.5rem;
        font-size: 1.25rem;
        line-height: 130%;
        margin: 0;
        span {
          font-weight: 600;
          color: #2F7A76;
        }
      }
    }
  }
}
.advantages {
  background: #F1F1F1;
  color: #333333;
  padding: 8rem 0 6.625rem;
  &__wrap {
    display: flex;
    justify-content: space-between;
    gap: 2rem;
  }
  &__left {
    max-width: 40rem;
  }
  &__right {
    max-width: 49rem;
  }
  h2 {
    text-stroke: 0.125rem #2F7A76;
    -webkit-text-stroke: 0.125rem #2F7A76;
    color: #2F7A76;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 1rem;
    margin-top: 2rem;
    p {
      font-size: 1.5rem;
      line-height: 130%;
      margin: 0;
    }
  }
  &__slider {
    position: relative;
    display: flex;
    gap: 3.125rem;
  }
  .swiper-wrapper {
    align-items: stretch;
  }
  .swiper-slide {
    height: auto;
    img {
      width: 100%;
      border-radius: 1.5rem;
    }
  }
  &__slider-nav {
    display: flex;
    gap: 0.875rem;
  }
  .swiper-button-prev,
  .swiper-button-next {
    position: static;
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 1rem / 3));
    gap: 0.5rem;
    align-items: start;
    margin-top: 3.125rem;
  }
  &__item {
    position: relative;
    padding: 2.8125rem;
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      transform: rotate(-6deg);
      width: 100%;
      height: 100%;
      border: 0.0625rem solid #2F7A76;
      border-radius: 50%;
      pointer-events: none;
    }
    p {
      max-width: 22rem;
      font-size: 1.5rem;
      line-height: 130%;
      margin: 0 auto;
    }
  }
}
.requests {
  padding: 8rem 0 13.75rem;
  h2 {
    text-align: center;
    text-stroke: 0.125rem #000;
    -webkit-text-stroke: 0.125rem #000;
    .text_color {
      line-height: 110%;
      color: #2F7A76;
      text-stroke: 0.125rem #2F7A76;
      -webkit-text-stroke: 0.125rem #2F7A76;
      margin: 0;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(5, 20%);
    align-items: start;
    margin-top: 1.5rem;
  }
  &__item {
    position: relative;
    &:nth-child(1) {
      margin-top: 6rem;
      p {
        top: 50%;
        left: 0.625rem;
        transform: translate(0, -50%);
      }
    }
    &:nth-child(2) {
      p {
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
    &:nth-child(3) {
      margin-top: 3.125rem;
      p {
        top: 4.5625rem;
        left: 50%;
        transform: translate(-50%, 0);
      }
    }
    &:nth-child(4) {
      margin-top: 2rem;
      p {
        top: 3.125rem;
        left: 55%;
        transform: translate(-50%, 0);
      }
    }
    &:nth-child(5) {
      margin-top: 6rem;
      p {
        top: 50%;
        left: 60%;
        transform: translate(-50%, -50%);
      }
    }
    &:nth-child(6) {
      max-width: 22.0625rem;
      grid-column: 1/3;
      margin-top: -3rem;
      p {
        top: 3.125rem;
        left: 1rem;
      }
    }
    &:nth-child(8) {
      max-width: 26.75rem;
      grid-column: 4/6;
      margin-top: -3rem;
      margin-left: auto;
      p {
        top: 50%;
        right: 1rem;
        transform: translate(0, -50%);
      }
    }
    &:nth-child(9) {
      max-width: 22.875rem;
      grid-column: 1/3;
      margin-top: -2.5rem;
      p {
        top: 60%;
        left: 2rem;
        transform: translate(0, -50%);
      }
    }
    &:nth-child(11) {
      max-width: 22.75rem;
      grid-column: 4/6;
      margin-top: -2.5rem;
      margin-left: auto;
      p {
        top: 45%;
        right: 2rem;
        transform: translate(0, -50%);
      }
    }
    p {
      position: absolute;
      z-index: 2;
      width: 100%;
      max-width: 13.4375rem;
      text-align: center;
      font-size: 1.125rem;
      line-height: 130%;
      color: #333333;
      margin: 0 auto;
    }
    &-img {
      pointer-events: none;
      img {
        width: 100%;
      }
    }
  }
  &__group {
    max-width: 38.75rem;
    display: flex;
    align-items: center;
    gap: 1.25rem;
    background: #fff;
    border-radius: 4.75rem;
    padding: 0.75rem 1rem;
    margin: 0 auto;
  }
  &__btn {
    flex-shrink: 0;
  }
  &__info {
    font-size: 1rem;
    line-height: 130%;
    color: #333333;
  }
  &__img {
    position: absolute;
    left: 0;
    bottom: 5.75rem;
    width: 100%;
    pointer-events: none;
  }
}
