      position: relative;
      font-size: 1.125rem;
      line-height: 130%;
      padding-left: 0.875rem;
      &:after {
        content: '';
        position: absolute;
        top: 0.5rem;
        left: 0;
        width: 0.25rem;
        height: 0.25rem;
        background: #333333;
        border-radius: 50%;
      }
    }
    li:nth-child(4) ~ li {
